}

.frame {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 400px;
  height: 400px;
  margin-top: -200px;
  margin-left: -200px;
  border-radius: 2px;
  box-shadow: 0.5rem 0.5rem 1rem rgba(182, 12, 197, 0.6);
  background-image: linear-gradient(to top, #6e0bdf, #955eee, #272232, #140e24);
  color: #4b384c;
  font-family: "overlock", Helvetica, sans-serif;
  font-weight: 300;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow: hidden;
}

.front {
  position: absolute;
  z-index: 10;
  width: 400px;
  height: 181px;
  left: 0;
  bottom: -53px;
  background: #fff;
  box-shadow: -1rem 0 1rem rgba(34, 33, 33, 0.5);
  -webkit-animation: slide 1s;
          animation: slide 1s;
  transition: 1s ease-in-out;
  z-index: 1000;
}

.front .temperature {
  float: left;
  margin-left: 18px;
  font-size: 57px;
  line-height: 120px;
}
.front .info {
  float: left;
  margin: 2rem 0 0 3rem;
  font-size: 1.2rem;
  line-height: 20px;
}

.front .preview {
  float: right;
  font-weight: 400;
  text-align: right;
  font-size: 1.2rem;
  line-height: 20px;
  margin: 25px 20px 0 0;
}
.front .preview td {
  padding: 0 3px;
  text-transform: uppercase;
}
#searchVal {
  text-transform: capitalize;
}
#Error_msg {
  color: red;
  font-weight: bold;
  font-size: 36px;
  margin-top: 78px;
}
